@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

html {
  height: 100%; }

body {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
  background-color: #ffffff; }

a {
  display: block;
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header--background {
  position: absolute;
  width: 100%;
  min-width: 320px;
  background: linear-gradient(#041827 84.9%, rgba(4, 24, 39, 0) 100%); }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  margin: auto;
  padding: 30px 0 29px;
  max-width: min(88%, 1160px); }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      padding: 19px 0 20px;
      max-width: min(86.5%, 1020px); } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding: 15px 0 16px;
      max-width: min(83%, 760px); } }

@media (max-width: 767px) {
  .page-header__logo img {
    width: 35px;
    height: 35px; } }

.page-header__contacts {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 41.5%; }
  @media (max-width: 1023px) {
    .page-header__contacts {
      width: 15%; } }
  @media (max-width: 767px) {
    .page-header__contacts {
      max-width: 100px;
      width: 38.6%; } }

.page-header__button {
  align-self: center;
  width: 160px;
  height: 40px;
  padding: 10px 15px;
  margin-right: 17px;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17.92px;
  text-transform: uppercase;
  color: #f2f5f6;
  background-color: transparent;
  border: 1px solid #f2f5f6;
  border-radius: 3px; }
  @media (max-width: 1023px) {
    .page-header__button {
      display: none; } }

.contacts__email {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 10px;
  margin-right: 5px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 17.92px;
  color: #f2f5f6; }

@media (max-width: 1023px) {
  .contacts__email span {
    display: none; } }

.contacts__email svg {
  margin-right: 15px; }
  @media (max-width: 1023px) {
    .contacts__email svg {
      transform: translate(-4px, -10px);
      margin-right: 0; } }
  @media (max-width: 767px) {
    .contacts__email svg {
      transform: translate(-8px, -6px); } }

.contacts__phone {
  position: relative;
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  align-items: center;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 17.92px;
  color: #f2f5f6; }

.contacts__phone svg {
  margin-right: 15px; }
  @media (max-width: 1023px) {
    .contacts__phone svg {
      transform: translate(-3px, -10px);
      margin-right: 0; } }
  @media (max-width: 767px) {
    .contacts__phone svg {
      transform: translate(-8px, -6px); } }

@media (max-width: 1023px) {
  .contacts__phone span {
    display: none; } }

.inner-header--background {
  width: 100%;
  min-height: 880px;
  box-sizing: border-box;
  background-color: #041827;
  background: linear-gradient(rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/main-background-desktop.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  @media (min-resolution: 2dppx) {
    .inner-header--background {
      background: linear-gradient(rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/main-background-desktop@2x.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover; } }
  @media (max-width: 1023px) {
    .inner-header--background {
      min-height: 640px;
      background: linear-gradient(rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/main-background-tablet@2x.webp"); } }
  @media (max-width: 767px) {
    .inner-header--background {
      min-height: 560px;
      background: linear-gradient(rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/main-background-mobile@2x.webp"); } }

.inner-header__wrapper {
  padding: 305px 0;
  margin: 0 auto;
  box-sizing: border-box;
  min-width: 890px;
  max-width: min(50.5%, 970px); }
  @media (max-width: 1023px) {
